import React, { useState, useEffect } from 'react';
import {
  Main,
  Box,
  Table,
  Thead,
  Tbody,
  Tr,
  Td,
  Th,
  Loader,
  Typography,
} from '@strapi/design-system';
import { useIntl } from 'react-intl';
import { getTranslation } from '../utils/getTranslation';

const HomePage = () => {
  const { formatMessage } = useIntl();
  const [payments, setPayments] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const response = await fetch('/stripe-dashboard/stats');
        const result = await response.json();
        setPayments(result.data);
      } catch (error) {
        console.error(error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchStats();
  }, []);

  if (isLoading) return (
    <Box padding={8} background="neutral100">
      <Loader>Loading content...</Loader>
    </Box>
  );

  return (
    <Main>
      <Typography variant="alpha">
        Welcome to {formatMessage({ id: 'plugin.name' })}
      </Typography>

      <Box
        padding={4}
        marginTop={4}
        background="neutral0"
        hasRadius
        shadow="tableShadow"
      >
        <Typography variant="beta" as="h2" style={{ marginBottom: '1rem' }}>
          Stripe Payments
        </Typography>

        <Table colCount={4} rowCount={payments.length}>
          <Thead>
            <Tr>
              <Th>ID</Th>
              <Th>Amount</Th>
              <Th>Status</Th>
              <Th>Date</Th>
            </Tr>
          </Thead>
          <Tbody>
            {payments.map((payment) => (
              <Tr key={payment.id}>
                <Td>{payment.id}</Td>
                <Td>â‚¬{Number(payment.amount).toFixed(2)}</Td>
                <Td>{payment.status}</Td>
                <Td>
                  {new Date(payment.date).toLocaleDateString(undefined, {
                    year: 'numeric',
                    month: 'short',
                    day: 'numeric',
                  })}
                </Td>
              </Tr>
            ))}
          </Tbody>
        </Table>
      </Box>
    </Main>
  );
};

export { HomePage };

